@font-face {
  font-family: "FerroRosso";
  src: url("../fonts/FerroRosso.ttf"); }

* {
  list-style: none; }

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white;
  background-color: #19424a;
  font-size: 1rem; }
  @media (min-width: 600px) {
    body {
      font-size: 1.25rem; } }
  @media (min-width: 800px) {
    body {
      font-size: 1.5rem; } }
  @media (min-width: 1200px) {
    body {
      font-size: 1.75rem; } }

.btn {
  cursor: pointer;
  margin-right: 0.5rem; }
  .btn:hover {
    background-color: #724d4b; }
  @media (min-width: 600px) {
    .btn {
      font-size: 1.25rem; } }
  @media (min-width: 800px) {
    .btn {
      font-size: 1.5rem; } }

.ACTunes_body {
  background-color: #19424a; }
  .ACTunes_body header img {
    margin-top: 1rem;
    width: 20rem; }
  .ACTunes_body .weatherIcon {
    background-color: ghostwhite;
    width: 2rem;
    padding: 5px;
    cursor: pointer;
    margin: 0.5em; }
  .ACTunes_body .clock {
    position: relative;
    text-align: center;
    width: 20rem;
    margin: auto; }
    .ACTunes_body .clock img {
      width: 100%; }
    .ACTunes_body .clock_time {
      position: absolute;
      bottom: 12%;
      left: 14%;
      color: black;
      font-size: 3.5rem;
      transform: rotate(24deg); }
  .ACTunes_body #player {
    background: whitesmoke;
    width: 25rem;
    margin: 0; }
  .ACTunes_body #weatherForm input {
    color: white;
    background-color: rgba(255, 255, 255, 0.1);
    width: 20rem;
    text-align: center;
    display: block;
    margin: auto; }
  .ACTunes_body #weatherForm .btn {
    margin-top: 1rem; }
  .ACTunes_body .geo {
    color: black;
    padding: 0; }
  .ACTunes_body footer,
  .ACTunes_body .timeline {
    margin-top: 1rem; }
  .ACTunes_body footer {
    margin-bottom: 3rem; }
  .ACTunes_body .downloads {
    padding: 0; }
    .ACTunes_body .downloads select {
      display: block; }
    .ACTunes_body .downloads form {
      display: flex; }
    .ACTunes_body .downloads_album {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around; }
    .ACTunes_body .downloads_title {
      flex-basis: 100%; }
